  position: absolute;
  width: 0;
  height: 0;
  border-left: 0px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 0px solid var(--medium-gray);
  transform: rotateX(90deg) rotateZ(-45deg);
}
